@import "../../styles/_variables.scss";

$formBlue: #F5FDFF;
$formBackgroundBlue: #E9FCFF;
$formGray: #989797;


.presentation {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  text-align: center;
  align-items: center;

  padding: 30px;

  min-height: 84vh;
  height: 100%;
  width: 100%;

  background: linear-gradient(103.67deg, rgba(219, 185, 240, 0) 16.86%, #C5D9E8 100%),
  linear-gradient(253.04deg, rgba(255, 243, 243, 0) 23.83%, #D1BBED 87.05%), #E9FCFF;

  font-family: Roboto, Arial, sans-serif;

  &__buttonLeft {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    height: 50px;
    width: 150px;
    background-color: $formBackgroundBlue;
    border-radius: 80px 10px 10px 80px;
    p {
      text-align: center;
      justify-self: center;
      align-self: center;
    }
  }

  &__buttonRight {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    height: 50px;
    width: 150px;
    background-color: $formBackgroundBlue;
    border-radius: 10px 80px 80px 10px;
    cursor: pointer;
    p {
      text-align: center;
      justify-self: center;
      align-self: center;
      text-decoration: none;
    }
  }


  &__heading {

    display: flex;
    flex-direction: column;
    padding-bottom: 5px;
    margin-bottom: 25px;
    border-bottom: cornflowerblue 1px solid;

    width: 100%;
    text-align: center;

    font-weight: bold;

    h1 {
      font-size: 28px;
      line-height: 32px;
      text-align: left;
    }

  }

  &__line{
    display: flex;
    flex-direction: row;
    font-size: 22px;
    line-height: 26px;
    text-align: left;
    margin-bottom: 10px;
    img {
      height: 20px;
      width: 20px;
      margin-left: 10px;
      margin-right: 10px;
    }
    i{
      font-style: italic;
    }
    &__inner{
      margin-left: 30px;
      margin-top: 5px;
    }
  }

  &__block {
    background: $formBlue;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);

    height: 500px;
    width: 850px;

    border-radius: 50px;
    padding: 40px;
  }

}
.presentation__block__first{
  background: $formBlue;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);

  height: 500px;
  width: 850px;

  border-radius: 50px;

  .first{
    width: 100%;
    height: 100%;
    background: url("../../assets/photos/alps2.png") no-repeat center;
    border-radius: 50px;
    background-size: cover;
    color: black;
  }

  &__top {
    width: 100%;
    color: black;

    padding-left:  100px;
    padding-top:  180px;

    &__content {
      display: flex;
      flex-direction: column;

      &__top {
        display: flex;
        flex-direction: row;

        &__logo {
        }

        &__text {
          text-align: left;
          margin-left: 25px;

          &__name {
            font-weight: 900;
            font-size: 28px;
            line-height: 32px;
            letter-spacing: 0.128571px;
            margin-bottom: 15px;
          }

          &__phrase {
            text-align: left;
            font-weight: 900;
            font-size: 24px;
            line-height: 28px;
            letter-spacing: 0.128571px;
          }
        }
      }
    }
  }
}

.hidden{
  height: 50px;
  width: 150px;
}

