@import "../../styles/_variables.scss";

/*TODO: change button design if they are disabled (Register and Login forms)*/

.auth {
  grid-area: mainContent;

  padding-bottom: 50px;
  padding-top: 50px;

  display: grid;
  height: 100%;
  width: 100%;

  min-height: 84vh;

  background-color: $blue;
  background: url("../../assets/photos/community.png") no-repeat center;
  background-size: cover;

  &__container {

    display: flex;
    flex-direction: row;

    height: 100%;
    width: 100%;

    &__text {
      display: flex;

      justify-content: center;
      align-items: center;

      align-self: center;
      justify-self: center;

      height: 100%;
      width: 50%;

      color: white;
      font-weight: bold;

      &__block {
        display: flex;
        flex-direction: column;

        margin-left: 20px;
        margin-top: 20px;

        height: 160px;
        width: 500px;

        &__heading {
          text-align: left;

          margin-bottom: 15px;

          font-size: 28px;
          line-height: 30px;
        }

        &__description {
          text-align: left;

          font-size: 24px;
          line-height: 26px;
        }
      }

    }

    &__form {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;

      width: 50%;
      height: 100%;

      &__block {
        background-color: white;
        padding: 50px;
        margin-left: 20px;

        min-width: 60%;

        border-radius: 40px;

        h2 {
          text-align: center;
          margin-bottom: 20px;

          color: $semiBlack;
          font-weight: bold;
          font-size: 28px;
          line-height: 30px;
        }

        &--confirmation {
          display: flex;
          flex-direction: column;
          justify-items: center;
          align-items: center;

          padding-top: 40px;
          margin-bottom: 40px;
          max-width: 350px;

          svg {
            height: 50px;
            width: 50px;
            color: $green;
          }

          img{
            height: 50px;
            width: 50px;
          }

          h3 {
            text-align: center;
            margin-top: 15px;
            margin-bottom: 10px;

            font-size: 22px;
            line-height: 25px;
          }

          p {
            font-weight: 300;
            font-size: 16px;
            line-height: 22px;
            text-align: center;
            letter-spacing: 0.1px;
            color: rgba(0, 0, 0, 0.5);
          }

          b{
            font-weight: normal;
            color: $green;
            font-size: 16px;
            line-height: 22px;
            cursor: pointer;
          }
        }
      }

      button {
        width: 100%;
        background-color: $green;
        border: 1px $green solid;
        border-radius: 10px;
      }

      button:hover {
        background-color: $brightGreen;
        border: 1px $green solid;
      }

      button:active {
        background-color: $green;
        border: 1px $green solid;
      }

      button:focus {
        background-color: $green;
        border: 1px $green solid;
      }

      &--error {
        text-align: center;
        font-size: 14px;
        line-height: 19px;
        color: red;
        margin-bottom: 12px;
      }

      &__bottom {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        font-size: 16px;
        line-height: 19px;

        p {
          text-align: right;
          margin-right: 10px;
        }

        a {
          text-align: left;
          text-decoration: none;
          color: $green;
        }
      }
    }
  }
}